body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  -ms-overflow-style: none;
}

body::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

* {
  box-sizing: border-box;
}

.wrapper{display:inline-flex;margin-top:50px}.wrapper_Bottom{position:absolute;bottom:10px;z-index:110}.wrapper .icon{position:relative;background-color:#ffffff;color:#111;border-radius:50%;padding:15px;margin:10px;width:50px;height:50px;font-size:18px;display:flex;justify-content:center;align-items:center;flex-direction:column;box-shadow:0 10px 10px rgba(0,0,0,0.1);cursor:pointer;transition:all 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55)}.wrapper_Bottom .icon{width:30px;height:30px}a{color:inherit}.wrapper .tooltip{position:absolute;top:0;font-size:14px;padding:5px 8px;border-radius:5px;box-shadow:0 10px 10px rgba(0,0,0,0.1);opacity:0;pointer-events:none;width:150px !important;transition:all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55)}.wrapper .tooltip::before{position:absolute;content:"";height:8px;width:8px;background-color:#ffffff;bottom:-3px;left:50%;-webkit-transform:translate(-50%) rotate(45deg);transform:translate(-50%) rotate(45deg);transition:all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55)}.wrapper .icon:hover .tooltip{top:-45px;opacity:1;visibility:visible;pointer-events:auto}.wrapper .icon:hover span,.wrapper .icon:hover .tooltip{text-shadow:0px -1px 0px rgba(0,0,0,0.1)}.wrapper .Itroduction:hover,.wrapper .Itroduction:hover .tooltip,.wrapper .Itroduction:hover .tooltip::before{background-color:#d35f32;color:#ffffff}.wrapper .Portfolio:hover,.wrapper .Portfolio:hover .tooltip,.wrapper .Portfolio:hover .tooltip::before{background-color:#2895c4;color:#ffffff}.wrapper .Contact:hover,.wrapper .Contact:hover .tooltip,.wrapper .Contact:hover .tooltip::before{background-color:#e1306c;color:#ffffff}*{margin:0;padding:0;box-sizing:border-box}*:focus,*:active{outline:none !important;-webkit-tap-highlight-color:transparent}img{-webkit-backface-visibility:hidden;backface-visibility:hidden}html{font-size:calc(10px + 1vmin)}.App{text-align:center}.App-header{background-color:#333f50;min-height:100vh;display:flex;flex-direction:column;align-items:center;align-content:c