@media only screen and (max-width: 1600px) {
    .MainTxt {
        width: 70vw;
        height: 70vh;
        text-align: left;
    }
}

//portrait-直向；landscape-橫向
@media (max-width:1200px) and (orientation: portrait) {
    .MainTxt {
        height: 78vh;
    }
}

@media only screen and (max-width: 1200px) {
    .divSkill {
        flex-wrap: wrap;

        .SkillBlock {
            width: 47%;
            margin: 5px 0;

            .SkillTitle {
                font-size: 2rem;
                font-weight: bolder;
                text-align: center;
                padding: 40px 0 18px 0;
            }


            .SkillList {
                span {
                    color: black;
                    text-align: center;
                    display: block;
                    margin: 5px;
                    font-weight: 500;
                }
            }
        }
    }
}